import request from '@/utils/request'

export function getResourceCategories( param ) {
  return request({
    url: '/resource/category',
    method: 'get',
    params: param
  })
}

export function getResourceList( param ) {
  return request({
    url: '/resource/list',
    method: 'get',
    params: param
  })
}

export function saveAdvise( data ) {
  return request({
    url: '/resource/correct',
    method: 'post',
    data: data
  })
}

export function getResource( params ) {
  return request({
    url: '/resource/info',
    method: 'get',
    params: params
  })
}

export function saveFavorite( params ) {
  return request({
    url: '/resource/favorite',
    method: 'get',
    params: params
  })
}